@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@700&display=swap');

.header__title {
  color: #0d065f;
  transform: translate(-50%, -50%);
  transition-duration: 2000ms;
  animation: textSliding 5s linear infinite;
}

@keyframes textSliding {
  0% {
    transform: translateX(5vw);
  }

  50% {
    transform: translateX(50vw);
  }

  100% {
    transform: translateX(5vw);
  }
}

.header__logo-container {
  position: absolute;
  font-family: 'Montserrat Alternates', sans-serif;
  font-size: 5vh;
  color: #ffad16;
  text-shadow: 2px 2px 4px #ffad16;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 5vh;
  width: 30vh;
  height: 18vh;
  background-image: linear-gradient(to right, #020024 0%, #090979 35%, #00d4ff 100%);
  border-radius: 30px;
  transition-property: background-image, text-shadow, color, transform;
  transition-timing-function: linear;
  transition-duration: 600ms;
  &:focus,
  &:hover {
    background-image: linear-gradient(to right, #00d4ff 0%, #090979 65%, #020024 100%);
    color: #15d315;
    text-shadow: 2px 2px 4px #15d315;
  }
}

.header {
  &__logo-on {
    position: relative;
    right: 5vh;
    font-family: 'Montserrat Alternates', sans-serif;
  }
}

.header__logo-container::after {
  content: 'soft';
  position: absolute;
  left: 15vh;
  display: inline-block;
  transition-delay: 300ms;
  transition-property: text-shadow, color, transform;
  transition-timing-function: linear;
  transition-duration: 600ms;
  color: #15d315;
  text-shadow: 2px 2px 4px #15d315;
}

.header__logo-container:focus::after,
.header__logo-container:hover::after {
  content: 'soft';
  position: absolute;
  left: 15vh;
  display: inline-block;
  transform: rotate(360deg);
  text-shadow: 2px 2px 4px #ffad16;
  color: #ffad16;
}

.header__menu-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 20vh;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
}

.header__menu {
  position: absolute;
  right: 20vw;
  top: 11vh;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}

.header__menu-link {
  position: relative;
  color: #fff;
  text-decoration: none;
  display: inline-block;
  padding: 10px 20px;
  font-weight: 500;
  background: rgba(212, 94, 94, 0.2);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid rgba(96, 91, 91, 0.1);
  z-index: 1000;
  overflow: hidden;
  backdrop-filter: blur(15px);

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50%;
    background: rgba(255, 255, 255, 0.1);
  }
}

.header__menu-item .header__menu-link {
  border-bottom-left-radius: 30px;
  border-top-left-radius: 30px;
  border-radius: 30px;
  margin-right: 20px;
}

.header__menu-item:nth-last-child(2) .header__menu-link {
  border-bottom-right-radius: 30px;
  border-top-right-radius: 30px;
}

#marker {
  position: absolute;
  top: 0;
  width: 41px;
  height: 41px;
  z-index: 1;
  transition: 0.5s;
}

#marker::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  z-index: 1;
  transition: 0.5s;
  background: cyan;
  box-shadow: 0 0 15px cyan, 0 0 30px cyan, 0 0 45px cyan, 0 0 60px cyan;
}

.header__menu-item:nth-child(2):hover ~ #marker::before {
  background: red;
  box-shadow: 0 0 15px red, 0 0 30px red, 0 0 45px red, 0 0 60px red;
}

.header__menu-item:nth-child(3):hover ~ #marker::before {
  background: yellow;
  box-shadow: 0 0 15px yellow, 0 0 30px yellow, 0 0 45px yellow, 0 0 60px yellow;
}

.header__menu-item:nth-child(4):hover ~ #marker::before {
  background: green;
  box-shadow: 0 0 15px green, 0 0 30px green, 0 0 45px green, 0 0 60px green;
}

.header__menu-item:nth-child(5):hover ~ #marker::before {
  background: silver;
  box-shadow: 0 0 15px silver, 0 0 30px silver, 0 0 45px silver, 0 0 60px silver;
}
